          clearable
          label=""
          placeholder=""
        >
          <van-button slot="button" size="small" type="info" plain @click="checkCarNum"></van-button>
        </van-field>
      </van-cell-group>
    </div>
    <div class="uploader-wrap">
      <div class="uploader-title"></div>
      <van-uploader :after-read="afterRead" v-model="images" :max-count="3" preview-size="106px" />
    </div>
    <!-- <div>
      <img :src="testImg" alt="" />
    </div> -->
    <div class="bottom">
      <div class="next-btn" @click="toNextPage"></div>
    </div>

    <van-popup v-model="isShowCertificateTypePicker" position="bottom">
      <van-picker
        show-toolbar
        :columns="certificateTypeOptions"
        @cancel="isShowCertificateTypePicker = false"
        @confirm="onCertificateTypePickerConfirm"
      />
    </van-popup>
    <van-popup v-model="isShowPersonnelTypePicker" position="bottom">
      <van-picker
        show-toolbar
        :columns="personnelTypeOptions"
        @cancel="isShowPersonnelTypePicker = false"
        @confirm="onPersonnelTypePickerConfirm"
      />
    </van-popup>
    <van-popup v-model="isShowTransportPicker" position="bottom">
      <van-picker
        show-toolbar
        :columns="transportOptions"
        @cancel="isShowTransportPicker = false"
        @confirm="onTransportPickerConfirm"
      />
    </van-popup>
  </div>
</template>

