export default {
  name: 'inner',
  data() {
    return {
      name: '',
      certificateType: '',
      isShowCertificateTypePicker: false,
      certificateTypeOptions: [
        { text: '', value: '' },
        { text: '', value: '' }
      ],
      idcard: '',
      personnelType: '',
      isShowPersonnelTypePicker: false,
      personnelTypeOptions: [
        { text: '', value: '' },
        { text: '', value: '' }
      ],
      phone: '',
      transportCode: '',
      isShowTransportPicker: false,
      transports: [],
      carNum: '',

      images: []
      // testImg: ''
    }
  },
  computed: {
    isPersonalType() {
      return this.personnelType === ''
    },
    transportOptions() {
      if (this.transports.length) {
        return this.transports.map(item => {
          return {
            value: item.vehicleCode,
            text: item.vehicleName
          }
        })
      }
      return []
    },
    transport() {
      if (this.transports.length && this.transportCode) {
        return _.find(this.transports, item => item.vehicleCode === this.transportCode)
      }
      return {}
    }
  },

  created() {
    this.getTransport()
  },
  methods: {
    ...mapMutations('punishment', ['PUT_PERSON_INFO', 'PUT_ILLEGE_BASE_INFO']),
    async toNextPage() {
      if (!this.valid()) {
        return
      }
      const { name, certificateType, idcard, phone, carNum, transport, personnelType, images } = this
      this.PUT_PERSON_INFO({
        name,
        certificateType,
        idcard
      })
      this.PUT_ILLEGE_BASE_INFO({
        personnelType,
        phone,
        transport,
        carNum,
        images
      })

      this.$router.push({
        name: 'illegal-punish'
      })
    },
    async afterRead(file) {
      // 
      // const imgFile = await compressImageFile({
      //   file: file.file,
      //   maxWidth: 1000,
      //   compressLevel: 0.9
      // })
      // var objectURL = window.URL.createObjectURL(imgFile)
      // this.testImg = objectURL
    },
    showCertificateTypePicker() {
      this.isShowCertificateTypePicker = true
    },
    onCertificateTypePickerConfirm(value) {
      this.certificateType = value.value
      this.isShowCertificateTypePicker = false
    },
    showPersonnelTypePicker() {
      this.isShowPersonnelTypePicker = true
    },
    onPersonnelTypePickerConfirm(value) {
      this.personnelType = value.value
      this.isShowPersonnelTypePicker = false
    },
    showTransportPicker() {
      this.isShowTransportPicker = true
    },
    onTransportPickerConfirm(value) {
      this.transportCode = value.value
      this.isShowTransportPicker = false
    },
    valid() {
      let passed = true
      const { name, certificateType, idcard, phone, carNum, transport, personnelType } = this
      let paris = [
        { value: name, txt: '' },
        { value: idcard, txt: '' },
        { value: personnelType, txt: '' }
      ]
      if (!this.isPersonalType) {
        paris = paris.concat([
          { value: transport, txt: '' },
          { value: carNum, txt: '' }
        ])
      }
      for (let i = 0; i < paris.length; i++) {
        const item = paris[i]
        if (isEmpty(item.value)) {
          Toast.fail(item.txt)
          passed = false
          break
        }
      }
      if (!passed) {
        return false
      }
      if (!valiIdCard(idcard)) {
        Toast.fail('')
        return false
      }
      if (phone && !valiPhone(phone)) {
        Toast.fail('')
        return false
      }
      if (!this.isPersonalType && !valiCarNumber(carNum)) {
        Toast.fail('')
        return
      }
      return true
    },
    async getTransport() {
      const params = {
        pageNo: 1,
        pageSize: 500,
        vehicleCode: '',
        vehicleName: ''
      }
      const response = await request(`/api/tVehicleType/searchPage?${qs.stringify(params)}`, {
        method: 'post'
      })
      if (response.resultCode === 200) {
        this.transports = response.data.records
      }
    },
    async checkIdentity() {
      const { name, idcard, certificateType } = this
      if (name.length < 1) {
        Toast.fail('')
        return false
      }
      if (idcard.length < 1) {
        Toast.fail('')
        return false
      }
      Toast.loading({
        message: '...',
        duration: 0,
        forbidClick: true
      })
      const params = {
        identityNumber: idcard
      }
      const response = await request(`/api/identity/identityVerify?${qs.stringify(params)}`, {
        method: 'post'
      })
      Toast.clear()
      if (response.resultCode === 200) {
        const checkResults = []
        for (let item in response.data) {
          checkResults.push({
            libName: item,
            result: response.data[item]
          })
        }
        this.$router.push({
          name: 'identity-check',
          params: {
            name,
            certificateType,
            idcard: this.idcard,
            checkResults
          }
        })
      } else {
        Toast.fail('')
      }
    },
    async checkCarNum() {
      const { carNum } = this
      if (!valiCarNumber(carNum)) {
        Toast.fail('')
        return
      }
      this.$router.push({
        name: 'search',
        params: {
          carNum
        }
      })
    }
  }
}
</script>

